import Vue from "vue";
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    routes:[
        {
            path:'/',
            name:'login',
            hidden:true,
            component:()=>import('@/components/Login')
        },
        {
            path:'/login',
            name:'login',
            hidden:true,
            component:()=>import('@/components/Login')
        },
        {
            path:'*',
            name:'NotFound',
            hidden:true,
            component:()=>import('@/components/NotFound')
        },
        {
            path:'/home',
            hidden:true,
            redirect:'/home/index',
            component:()=>import('@/components/Home'),
            children:[
                {
                path:'/home/index',
                component:()=>import('@/components/component/Home'),
                },
                {
                    path:'mc',
                    name:'我的世界热力图-小游戏',
                    component:()=>import('@/components/DataMap/McMap')
                },
                {
                    path:'1-1',
                    name:'微博热搜图',
                    component:()=>import('@/components/DataMap/weiboMap')
                },
                {
                    path:'1-2',
                    name:'树图',
                    component:()=>import('@/components/DataMap/codeMap')
                },
            ]
        },
        {
            path: "/home/Student",
            iconClass:'fa fa-users',
            name: '学生管理系统',
            component: () => import('@/components/Home'),
            children: [
                {
                    path: '/home/Student',
                    name: '学生管理',
                    iconClass:'fa fa-list',
                    redirect:'/home/StudentInfo',
                    component: () => import('@/components/Student/Student'),
                    children:[
                        {
                            path: '/home/StudentInfo',
                            name: '学生信息',
                            iconClass:'fa fa-list',
                            component: () => import('@/components/Student/StudentInfo'),
                        },
                        {
                            path: '/home/StudentInfos',
                            name: '信息管理',
                            iconClass:'fa fa-list',
                            component: () => import('@/components/Student/StudentInfos'),
                        },
                        {
                            path: '/home/StudentWork',
                            name: '作业管理',
                            iconClass:'fa fa-list',
                            component: () => import('@/components/Student/StudentWork'),
                        },
                    ],
                },
                {
                    path: '/home/StudentMap',
                    name: '地图概览',
                    iconClass:'fa fa-map',
                    redirect:'/home/DataMap',
                    component: () => import('@/components/Student/Student'),
                    children:[
                        {
                            path: '/home/DataMap',
                            name: '数据地图',
                            iconClass:'fa fa-map-marker',
                            component: () => import('@/components/DataMap/DataMap'),
                        },
                        {
                            path: '/home/WordMap',
                            name: '全国地图',
                            iconClass:'fa fa-map-marker',
                            component: () => import('@/components/DataMap/WordMap'),
                        },
                        {
                            path: '/home/StudentMap',
                            name: '学校地图',
                            iconClass:'fa fa-map-marker',
                            component: () => import('@/components/DataMap/StudentMap'),
                        },
                        {
                            path: '/home/StudentsMap',
                            name:'学生地图',
                            iconClass:'fa fa-map-marker',
                            component: () => import('@/components/DataMap/StudentsMap'),
                        }
                    ]
                },
            ],
        },
    ],
    mode: 'history',
})


